@mixin onMobile {
  @media (min-width: 360px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onUltraWide {
  @media (min-width: 2560px) {
    @content;
  }
}

@mixin onLandscape {
  @media (orientation: landscape) {
    @content;
  }
}
