* {
  box-sizing: border-box;
}

html {
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
input{
  margin: 0;
  padding: 0;
}

button {
  font-family: 'Nunito', sans-serif;
}